body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container {
  width: 1000px;
  height: 1000px;
  overflow: auto;
}

.cube {
  float: left;
  width: 150px; 
  height: 150px;
}

.simulationPanel{
  position: fixed;
  width: 1000px;
  height: 1000px;
  left: 10%;
  top: 0%;
  margin-left: 300px}

.inputParamPanel{
  font-size: large;
  position: fixed;
  width: 150px;
  height: 100px;
  left: 10%;
  top: 0%;
  margin-left: 0px
}